@import "../../common/common.scss";

.developer{
  width: 100%;
  height: 100vh;
  overflow: hidden;

  /* Web */
  @media screen and (min-width:768px) {
    .developer-inner{
      overflow: auto;
      padding-top: 50px;
      height: 100%;
      background-color: #f9fbff;
      h2{
        margin: 30px 0 20px;
        text-align: left;
        font-size: 30px;
      }
      .developer-info{
        position: relative;
        margin: 0 auto;
        max-width: 1080px;
        width: 80%;
  
        .myImg{
          position: absolute;
          left: 0;
          top: 70px;
          padding: 5px;
          width: 150px;
          border-radius: 5px;
          background: #fff;
          box-shadow: 5px 5px 10px rgba(135, 126, 126, 0.376);
        }
        .blog-link{
          position: absolute;
          left: 0;
          top: 300px;
          height: 100%;
          a{
            display: block;
            padding: 5px;
            margin-bottom: 10px;
            width: 150px;
            color: $font-color;
            font-weight: 400;
            border-radius: 3px;
            background-color: #fff;
            box-shadow: 3px 3px 10px rgba(99, 93, 93, 0.376);
            img{
              margin-right: 5px;
              width: 15px;
              transform: translateY(-2px);
            }
            &:hover{
              background-color: #ededed;
              transition: all 0.6s;
            }
          }
        }
        .profile{
          position: absolute;
          left: 200px;
          top: 80px;
          text-align: left;
          h3{
            padding-bottom: 5px;
            color: $back-color1;
            font-size: 20px;
            font-weight: 600;
            text-transform: uppercase;
          }
          
          > div{&:not(:first-child){margin-top: 30px;}}
          .about{}
          .contact{
            svg{
              margin-right: 10px;
              transform: translateY(3px);
            }
          }
          .skill{
              h3{margin-bottom: 10px;}
            .img-flex{
              display: flex;
              flex-wrap: wrap;
              width: 280px;
              div{
                overflow: hidden;
                margin-right: 10px;
                margin-bottom: 10px;
                width: 50px;
                height: 50px;
                border-radius: 10px;
                box-shadow: 5px 5px 3px rgba(135, 126, 126, 0.376);
                img{
                  width: 100%;
                  height: 100%;
                }
                &:first-child, &:nth-child(3){
                  img{
                    width: 40px;
                    height: 40px;
                    transform: translate(5px, 5px);
                  }
                  background-color: #3c3c3c;
                }
                &:nth-child(4){background-color: #13AA52;}
              }
            }
          }
          .experience p span{
            padding-bottom: 100px;
            margin-right: 20px;
          }
        }
      }
    }
  }
  /* 모바일 */
  @media screen and (max-width:767px) {
    .developer-inner{
      overflow: auto;
      height: 100%;
      background-color: #f9fbff;
      h2{
        margin: 15px 0 20px;
        text-align: left;
        font-size: 30px;
      }
      .developer-info{
        position: relative;
        margin: 0 auto;
        max-width: 1080px;
        width: 90%;
  
        .myImg{
          position: absolute;
          left: 0;
          padding: 5px;
          width: 140px;
          border-radius: 5px;
          background: #fff;
          box-shadow: 5px 5px 10px rgba(135, 126, 126, 0.376);
        }
        .blog-link{
          margin-top: 240px;
          width: 100%;
          height: 100%;
          a{
            display: block;
            padding: 5px 0;
            margin-bottom: 10px;
            width: 100%;
            color: $font-color;
            font-weight: 400;
            border-radius: 3px;
            background-color: #fff;
            box-shadow: 3px 3px 10px rgba(99, 93, 93, 0.376);
            img{
              margin-right: 5px;
              width: 15px;
              transform: translateY(-2px);
            }
            &:hover{
              background-color: #ededed;
              transition: all 0.6s;
            }
          }
        }
        .profile{
          text-align: left;
          h3{
            padding-bottom: 5px;
            color: $back-color1;
            font-size: 20px;
            font-weight: 600;
            text-transform: uppercase;
          }
          
          > div{&:not(:first-child){margin-top: 30px;}}
          .about{
            position: absolute;
            left: 170px;
            top: 60px;
          }
          .contact{
            svg{
              margin-right: 10px;
              transform: translateY(3px);
            }
          }
          .skill{
              h3{margin-bottom: 10px;}
            .img-flex{
              display: flex;
              flex-wrap: wrap;
              width: 280px;
              div{
                overflow: hidden;
                margin-right: 10px;
                margin-bottom: 10px;
                width: 50px;
                height: 50px;
                border-radius: 10px;
                box-shadow: 5px 5px 3px rgba(135, 126, 126, 0.376);
                img{
                  width: 100%;
                  height: 100%;
                }
                &:first-child, &:nth-child(3){
                  img{
                    width: 40px;
                    height: 40px;
                    transform: translate(5px, 5px);
                  }
                  background-color: #3c3c3c;
                }
                &:nth-child(4){background-color: #13AA52;}
              }
            }
          }
          .experience p{
            padding-bottom: 15px;
          }
          .experience p:last-child{padding-bottom: 100px;}
          .experience p span{
            display: block;
            padding-bottom: 5px;
            margin-bottom: 5px;
            border-bottom: 1px solid rgba(0, 0, 0, 0.4);
            
          }
        }
      }
    }
  }
}
// common scss variable
$font-xs: 12px; 
$font-sm: 14px;   // mobile-본문
$font-base: 16px; // web-본문
$font-lg: 18px;   // 소제목
$font-xl: 20px;   // 대제목
$font-NoSansKR: 'Noto Sans KR', sans-serif; //폰트
$font-color: rgba(0,0,0,0.80);
$back-color1: #79b1ff;
$back-color2: #dfe0ff;
$back-color3: #EFF0FF;